@import "../../vars.css";

.AboutItem {
    display: flex;
    flex-direction: row;
    align-items: center;
    border-radius: 20px;
    list-style: none;
    padding: 25px;
    background-image: linear-gradient(90deg, 
    rgba(var(--color-primary-rgb), 0.4) 0%, 
    rgba(255, 255, 255, 0) 100%);
    background-size: 0 100%;
    background-repeat: no-repeat;
    transition: 0.4s;
    gap: 20px;
}

.AboutItem:hover {
    background-size: 100% 100%;
}

.AboutItem h3{
    font-size: 25px;
    font-weight: 600;
}

.AboutItem p{
    font-size: 25px;
}

.AboutItem img{
    /* Colored SVG using https://codepen.io/sosuke/pen/Pjoqqp*/
    width: 80px;
    height: 80px;
    filter: invert(37%) sepia(11%) saturate(1959%) hue-rotate(75deg) brightness(101%) contrast(87%);
}

@media screen and (max-width: 830px) {
    .AboutItem {
        padding-left: 10px;
        padding-right: 10px;
    }
}
